import {
  datePgVacation,
  dateEnd,
} from "../vacationFunctions/vacationFunctions";
import { ValidationDatesVacation } from "./ValidationDatesVacation";
import { WarningNotes } from "../warningNotes";

const { validatorDate, validatorDays } = ValidationDatesVacation();

export function ValidationDayVacationP1() {
  const dayVacationP1 = (
    { getFieldValue, setFieldsValue, setFields },
    setEnaRow
  ) => ({
    validator(_, value) {
      const testDate = getFieldValue("dateBeginVacation01");
      console.log(value);
      const validDate = validatorDate(testDate, null);
      const totalDays = getFieldValue("vacationDays");

      const datePgo = datePgVacation(testDate._d);

      if (testDate.length === 0) {
        setFields([
          {
            name: "daysVacation01",
            value: totalDays,
          },
        ]);
        return Promise.reject(
          new Error(
            "Favor primeiro selecionar a data de inicio das férias corretamente."
          )
        );
      } else if (totalDays < 19) {
        const aviso =
          "Numero de dias de férias insuficientes para parcelar férias. Valor será em cota única ";
        WarningNotes(aviso);
        setFieldsValue({
          daysVacation01: totalDays,
        });
        return Promise.reject(new Error("Não é permitido parcelar"));
      } else {
        if (validDate) {
          //console.log("Parcela 01 = " + getFieldValue("daysVacation01"));
          //console.log("Parcela 02 = " + getFieldValue("daysVacation02"));
          //console.log("Parcela 03 = " + getFieldValue("daysVacation03"));
          const datEndP1 = dateEnd(Date.parse(testDate._d), value);
          if (
            value < totalDays &&
            value > totalDays - 2 &&
            getFieldValue("daysVacation02") === "" &&
            getFieldValue("daysVacation03") === ""
          ) {
            //console.log("Primeiro Click");
            const datEndP1 = dateEnd(Date.parse(testDate._d), totalDays - 5);

            const diffDays = validatorDays(
              totalDays - 5,
              "daysVacation01",
              getFieldValue("vacationDays"),
              getFieldValue("daysVacation01"),
              getFieldValue("daysVacation02"),
              _,
              setEnaRow
            );

            setFieldsValue({
              daysVacation01: totalDays - 5,
              dateEndVacation01: datEndP1,
              datePgto01: datePgo,
              daysVacation02: diffDays,
              dateEndVacation02: "",
              dateBeginVacation02: "",
              datePgto02: "",
              daysVacation03: "",
              dateEndVacation03: "",
              dateBeginVacation03: "",
              datePgto03: "",
            });

            /* Data inicio P2 nula  */
          } else if (
            getFieldValue("dateBeginVacation02") === "" &&
            getFieldValue("daysVacation03") === ""
          ) {
            //console.log("Data inicio P2 nula");

            if (
              getFieldValue("daysVacation02") <= 5 &&
              getFieldValue("daysVacation01") > totalDays - 6
            ) {
              //console.log("P1 > 15, P2 <= 5 e P3==0");
              setFieldsValue({
                daysVacation01: totalDays,
                dateEndVacation01: datEndP1,
                datePgto01: datePgo,
                daysVacation02: "",
                dateEndVacation02: "",
                dateBeginVacation02: "",
                datePgto02: "",
              });

              validatorDays(
                value,
                "daysVacation01",
                getFieldValue("vacationDays"),
                getFieldValue("daysVacation01"),
                getFieldValue("daysVacation02"),
                _,
                setEnaRow
              );
            } else {
              //console.log("dataInP2= e diasP2=0");
              const diffDays = validatorDays(
                value,
                "daysVacation01",
                getFieldValue("vacationDays"),
                getFieldValue("daysVacation01"),
                getFieldValue("daysVacation02"),
                _,
                setEnaRow
              );

              setFieldsValue({
                dateEndVacation01: datEndP1,
                datePgto01: datePgo,
                daysVacation02: diffDays,
                dateEndVacation02: "",
                dateBeginVacation02: "",
                datePgto02: "",
                daysVacation03: "",
                dateEndVacation03: "",
                dateBeginVacation03: "",
                datePgto03: "",
              });
            }
          } else if (
            getFieldValue("dateBeginVacation02") !== "" &&
            getFieldValue("dateBeginVacation03") === ""
          ) {
            //console.log("Possui Data inicio P2");
            const dateBegin02 = getFieldValue("dateBeginVacation02");
            const datePgo2 = datePgVacation(dateBegin02._d);
            const datEndP2 = dateEnd(Date.parse(dateBegin02._d), value);
            const diasP2 =
              totalDays -
              getFieldValue("daysVacation01") -
              getFieldValue("daysVacation03");

            if (getFieldValue("daysVacation03") === "") {
              const diffDays = validatorDays(
                value,
                "daysVacation01",
                getFieldValue("vacationDays"),
                getFieldValue("daysVacation01"),
                getFieldValue("daysVacation02"),
                _,
                setEnaRow
              );

              setFieldsValue({
                dateEndVacation01: datEndP1,
                datePgto01: datePgo,
                daysVacation02: diffDays,
                dateEndVacation02: datEndP2,
                dateBeginVacation02: dateBegin02,
                datePgto02: datePgo2,
                daysVacation03: "",
                dateEndVacation03: "",
                dateBeginVacation03: "",
                datePgto03: "",
              });
            }
            //Possui data P2 e P3
          } else if (
            getFieldValue("dateBeginVacation02") !== "" &&
            getFieldValue("dateBeginVacation03") !== ""
          ) {
            //console.log("Existe dataP2 e dataP3");
            //console.log(getFieldValue("daysVacation02"));
            const dateBegin02 = getFieldValue("dateBeginVacation02");
            const datePgo2 = datePgVacation(dateBegin02._d);
            const daysVac03 = getFieldValue("daysVacation03");
            const dateInP3 = getFieldValue("dateBeginVacation03");
            const datePgo3 = datePgVacation(dateInP3._d);
            const datEndP3 = dateEnd(Date.parse(dateInP3._d), daysVac03);

            if (
              getFieldValue("daysVacation01") > totalDays - 10 &&
              getFieldValue("daysVacation03") > 0 &&
              getFieldValue("daysVacation02") < 6
            ) {
              //console.log("Existe dataP2, dataP3 e diasP2 invalido");
              const dayVacP1 = totalDays - 5 - getFieldValue("daysVacation03");
              const datEndP2 = dateEnd(Date.parse(dateBegin02._d), 5);
              setFieldsValue({
                daysVacation01: dayVacP1,
                dateEndVacation01: datEndP1,
                datePgto01: datePgo,
                daysVacation02: 5,
                dateEndVacation02: datEndP2,
                dateBeginVacation02: dateBegin02,
                datePgto02: datePgo2,
                daysVacation03: daysVac03,
                dateEndVacation03: datEndP3,
                dateBeginVacation03: dateInP3,
                datePgto03: datePgo3,
              });

              return Promise.reject(
                new Error("Parcela 2 não pode ser menor que 5")
              );
            } else {
              //console.log("Existe dataP2, dataP3 e diasP2 valido");

              const diffDays = validatorDays(
                value,
                "daysVacation03",
                getFieldValue("vacationDays"),
                getFieldValue("daysVacation01"),
                getFieldValue("daysVacation02"),
                getFieldValue("daysVacation03"),
                setEnaRow
              );
              const datEndP2 = dateEnd(Date.parse(dateBegin02._d), diffDays);

              //console.log(diffDays);
              setFieldsValue({
                daysVacation01: value,
                dateEndVacation01: datEndP1,
                datePgto01: datePgo,
                daysVacation02: diffDays,
                dateEndVacation02: datEndP2,
                dateBeginVacation02: dateBegin02,
                datePgto02: datePgo2,
                daysVacation03: daysVac03,
                dateEndVacation03: datEndP3,
                dateBeginVacation03: dateInP3,
                datePgto03: datePgo3,
              });

              return Promise.resolve();
            }
          } else {
            return Promise.reject(
              new Error("Data está invalida. Favor corrigir")
            );
          }
        }
      }
    },
  });

  return { dayVacationP1 };
}
