import { Input, Form, Row, Col, Radio } from "antd";
import "./container.css";
import { useState } from "react";
import { FieldData } from "../../../util/containerInterface";

export default function ContainerIdentify(onlyBoss) {
  const [fields, setFields] = useState<FieldData[]>([
    { name: "fullname", value: "" },
    { name: "registry", value: "" },
    { name: "subsidiary", value: "" },
    { name: "office", value: "" },
    { name: "boss", value: "" },
    { name: "who", value: "" },
  ]);

  const [radioButton, setRadioButton] = useState("ownVacation");
  const [form] = Form.useForm();

  const options = [
    { label: "Própria", value: "ownVacation" },
    { label: "Colaborador", value: "employeeVacation" },
  ];

  const onChangeRadio = (value) => {
    form.setFieldsValue({ who: value.target.value });
    form.submit();
    setRadioButton(value.target.value);
  };
  return (
    <>
      <fieldset>
        <legend>Identificação do Solicitante</legend>
        <Form
          name="formIdentify"
          labelCol={{ span: 10 }}
          wrapperCol={{ span: 12 }}
          labelAlign="left"
          fields={fields}
          form={form}
          onValuesChange={(_, allFields) => {
            //setFields(allFields);
            //console.log(fields);
          }}
        >
          <Row gutter={50}>
            <Col span={12}>
              <Form.Item name={"fullname"} label={"Nome Solicitante"}>
                <Input />
              </Form.Item>
            </Col>
            <Col span={12}>
              <Form.Item name={"registry"} label={"Matrícula"}>
                <Input readOnly />
              </Form.Item>
            </Col>
          </Row>
          <Row gutter={50}>
            <Col span={12}>
              <Form.Item name={"office"} label={"Cargo Solicitante"}>
                <Input readOnly />
              </Form.Item>
            </Col>
            <Col span={12}>
              <Form.Item name={"subsidiary"} label={"Filial"}>
                <Input readOnly />
              </Form.Item>
            </Col>
          </Row>
          <Row gutter={50}>
            <Col span={12}>
              <Form.Item name={"boss"} label={"Superior Imediato"}>
                <Input readOnly />
              </Form.Item>
            </Col>
            <Col span={12}>
              <span hidden={onlyBoss.onlyBoss}>
                <Form.Item
                  name={"who"}
                  label={"Solicitação de Férias"}
                  labelAlign="left"
                  valuePropName="checked"
                >
                  <Radio.Group
                    value={radioButton}
                    optionType="button"
                    buttonStyle="solid"
                    onChange={onChangeRadio}
                    options={options}
                    defaultValue="ownVacation"
                  />
                </Form.Item>
              </span>
            </Col>
          </Row>
        </Form>
      </fieldset>
    </>
  );
}
