import {
  datePgVacation,
  dateEnd,
} from "../vacationFunctions/vacationFunctions";
import { ValidationDatesVacation } from "./ValidationDatesVacation";

const { validatorDate } = ValidationDatesVacation();

export function ValidationDateP1() {
  const dateValidationP1 = ({ getFieldValue, setFieldsValue }) => ({
    validator(_, value) {
      const validDate = validatorDate(value, null);
      let datEnd = "";
      const datePgo = datePgVacation(value._d);
      //console.log(validDate);
      if (validDate) {
        const vacationDay = getFieldValue("daysVacation01");

        if (vacationDay) {
          datEnd = dateEnd(Date.parse(value._d), vacationDay);
        }
        //console.log(datEnd);
        //console.log(value._d);
        setFieldsValue({
          dateEndVacation01: datEnd,
          datePgto01: datePgo,
        });
        return Promise.resolve(
          setFieldsValue({
            dateEndVacation01: datEnd,
            datePgto01: datePgo,
          })
        );
      } else {
        return Promise.reject(
          new Error("Data negada. Favor digitar uma data correta")
        );
      }
    },
  });

  return { dateValidationP1 };
}
