import {
  datePgVacation,
  dateEnd,
} from "../vacationFunctions/vacationFunctions";
import { ValidationDatesVacation } from "./ValidationDatesVacation";
import { WarningNotes } from "../warningNotes";

const { validatorDate } = ValidationDatesVacation();

export function ValidationDateP3() {
  const dateValidationP3 = ({ getFieldValue, setFieldsValue }) => ({
    validator(_, value) {
      const validDate = validatorDate(
        value,
        getFieldValue("dateEndVacation02")
      );
      let datEnd = "";
      //console.log(validDate);
      const datePgo3 = datePgVacation(value._d);
      if (
        getFieldValue("daysVacation01") < 14 &&
        getFieldValue("daysVacation02") < 14 &&
        getFieldValue("daysVacation03") < 14
      ) {
        const aviso =
          "Uma das parcela tem que maior ou igual a 14 dias. Favor corrigir ";
        WarningNotes(aviso);
        return Promise.reject(new Error("Parcelas não permitidas"));
      } else {
        if (validDate) {
          const vacationDay = getFieldValue("daysVacation03");

          if (vacationDay) {
            datEnd = dateEnd(Date.parse(value._d), vacationDay);
          }

          setFieldsValue({
            dateEndVacation03: datEnd,
            datePgto03: datePgo3,
          });
          return Promise.resolve(
            setFieldsValue({
              dateEndVacation03: datEnd,
              datePgto03: datePgo3,
            })
          );
        } else {
          return Promise.reject(
            new Error("Data negada. Favor digitar uma data correta")
          );
        }
      }
    },
  });

  return { dateValidationP3 };
}
