import { Form, Table } from "antd";
import { EditOutlined } from "@ant-design/icons";
import "../containersCommonFile/container.css";
import { useState } from "react";
import { FieldData } from "../../../util/containerInterface";
//import { convertStringDate } from "../../../util/historyInfoVacationFunction/historyInfoVacationFunction";
import { data } from "../../../util/historyInfoVacationRequest/historyInfoVacationRequest";

export default function ContainerInfoVacation() {
  const [fields, setFields] = useState<FieldData[]>([
    { name: "who", value: "" },
    { name: "tableInfo", value: {} },
  ]);

  //console.log(dataTable);

  interface iTable {
    rowSelection?: {} | undefined;
    scroll?: {} | undefined;
    hasData?: boolean;
    pagination?: {} | any;
  }

  const tableConfig: iTable = {
    hasData: true,
    rowSelection: undefined,
  };

  const columns = [
    {
      title: "Situação",
      dataIndex: "status",
      key: "key",
    },
    {
      title: "Período Aquisitivo Inicial",
      dataIndex: "perAqIn",
      key: "key",
    },
    {
      title: "Período Aquisitivo Final",
      dataIndex: "perAqEnd",
      key: "key",
    },
    {
      title: "Data Inicio",
      dataIndex: "dateIn",
      key: "key",
    },
    {
      title: "Data Fim",
      dataIndex: "dateEnd",
      key: "key",
    },
    {
      title: "Dias",
      dataIndex: "day",
      key: "key",
    },
    {
      title: "Faltas",
      dataIndex: "fault",
      key: "key",
    },
    {
      title: "13° Salário",
      dataIndex: "salary13",
      key: "key",
    },
    {
      title: "Abono",
      dataIndex: "allowence",
      key: "key",
    },

    {
      title: "Parcelas",
      dataIndex: "part",
      key: "key",
    },
    {
      title: "Ação",
      key: "action",
      render: (name) => {
        //console.log(name.status);
        //console.log(dateEnd);
        //console.log(name.part > 1);

        if (name.status === "Aberto") {
          return (
            <a>
              <EditOutlined />
            </a>
          );
        } else {
          return <span></span>;
        }
      },
    },
  ];

  const [form] = Form.useForm();

  const onRowSelection = (record, _) => {
    //console.log(index);
    //console.log(record);
    //console.log(record.part);
    form.setFieldsValue({ tableInfo: record });
    form.submit();
  };

  return (
    <>
      <fieldset>
        <legend>Informações de Férias</legend>
        <Form
          name="formInfoVacation"
          labelAlign="left"
          form={form}
          onFieldsChange={(_, allfields) => {
            setFields(allfields);
            //console.log(allfields);
          }}
        >
          <Form.Item name={"tableInfo"}></Form.Item>

          <Table
            {...tableConfig}
            pagination={false}
            dataSource={data}
            columns={columns}
            onRow={(record, index) => {
              return {
                onClick: (event) => onRowSelection(record, index),
              };
            }}
          />
        </Form>
      </fieldset>
    </>
  );
}
