import {
  datePgVacation,
  dateEnd,
} from "../vacationFunctions/vacationFunctions";
import { ValidationDatesVacation } from "./ValidationDatesVacation";
import { WarningNotes } from "../warningNotes";
//import { useEffect } from "react";
import moment from "moment";

const { validatorDate, validatorDays } = ValidationDatesVacation();

export function ValidationDayVacationP2() {
  const dayVacationP2 = (
    { getFieldValue, setFieldsValue, setFields },
    setEnaRow
  ) => ({
    validator(_, value) {
      //console.log("Parcela 02");
      //console.log(getFieldValue("dateBeginVacation03"));
      const testDate = getFieldValue("dateBeginVacation02");
      const validDate = validatorDate(
        testDate,
        getFieldValue("dateEndVacation01")
      );
      const totalDays = getFieldValue("vacationDays");
      const datePgo2 = datePgVacation(testDate._d);

      //console.log(testDate);
      //console.log(validDate);
      //console.log(value);

      //Se o total de dias de férias for menor que 25 dias
      if (totalDays < 25) {
        const aviso =
          "Numero de dias de férias insuficientes para parcelar as férias em 3 vezes. ";
        WarningNotes(aviso);
        setFieldsValue({
          daysVacation02: totalDays - getFieldValue("daysVacation01"),
        });
        return Promise.reject(new Error("Não é permitido parcelar"));
      } else {
        //Se data P2 for válida
        if (validDate) {
          if (
            value <= totalDays - getFieldValue("daysVacation01") &&
            value >= totalDays - getFieldValue("daysVacation01") - 2
          ) {
            const datEndP2 = dateEnd(Date.parse(testDate._d), value - 4);
            console.log("Habilita Div 3");

            const data = moment(new Date());

            setFields([
              {
                name: "daysVacation02",
                value: value - 4,
              },
              {
                name: "dateEndVacation02",
                value: datEndP2,
              },
              {
                name: "dateBeginVacation03",
                value: "",
              },
              { name: "daysVacation03", value: 5 },
              { name: "dateEndVacation03", value: "" },
            ]);

            validatorDays(
              value - 5,
              "daysVacation02",
              getFieldValue("vacationDays"),
              getFieldValue("daysVacation01"),
              getFieldValue("daysVacation02"),
              _,
              setEnaRow
            );
            /* caso reduza valor P3<5 e P2 aumenta */
          } else if (
            getFieldValue("daysVacation03") < 6 &&
            getFieldValue("daysVacation02") >
              totalDays - getFieldValue("daysVacation01") - 5
          ) {
            //console.log("Parcela 3 < 5 Desabilitar div 3 .");
            const daysVac02 =
              getFieldValue("vacationDays") - getFieldValue("daysVacation01");

            const datEndP2 = dateEnd(Date.parse(testDate._d), daysVac02);

            setFields([
              {
                name: "daysVacation02",
                value: daysVac02,
              },
              {
                name: "dateEndVacation02",
                value: datEndP2,
              },
              {
                name: "datePgto02",
                value: datePgo2,
              },
              { name: "daysVacation03", value: "" },
              { name: "dateBeginVacation03", value: "" },
              { name: "dateEndVacation03", value: "" },
              { name: "datePgto03", value: "" },
            ]);

            validatorDays(
              daysVac02,
              "daysVacation02",
              getFieldValue("vacationDays"),
              getFieldValue("daysVacation01"),
              getFieldValue("daysVacation02"),
              _,
              setEnaRow
            );
          } else if (
            getFieldValue("daysVacation02") < 6 &&
            getFieldValue("daysVacation03") < 6
          ) {
            return Promise.reject();
            /*Se ok  */
          } else {
            const diffDays = validatorDays(
              value,
              "daysVacation02",
              getFieldValue("vacationDays"),
              getFieldValue("daysVacation01"),
              getFieldValue("daysVacation02"),
              _,
              setEnaRow
            );
            const datEndP2 = dateEnd(Date.parse(testDate._d), value);

            // Sem data de incio da parcela 3
            if (getFieldValue("dateBeginVacation03") === "" || undefined) {
              console.log(" Data Parcela 3 sem valor");
              const diffDays = validatorDays(
                value,
                "daysVacation02",
                getFieldValue("vacationDays"),
                getFieldValue("daysVacation01"),
                getFieldValue("daysVacation02"),
                _,
                setEnaRow
              );
              const datEndP2 = dateEnd(Date.parse(testDate._d), value);
              /* const datEndP3 = dateEnd(
                Date.parse(getFieldValue("dateBeginVacation03")._d),
                diffDays
              );

              const datePgo3 = datePgVacation(
                getFieldValue("dateBeginVacation03")._d
              ); */
              setFieldsValue({
                dateEndVacation02: datEndP2,
                datePgto02: datePgo2,
                daysVacation03: diffDays,
                dateEndVacation03: "",
                datePgto03: "",
              });
            } else {
              const datEndP3 = dateEnd(
                Date.parse(getFieldValue("dateBeginVacation03")._d),
                diffDays
              );
              console.log("Geral");

              const datePgo3 = datePgVacation(
                getFieldValue("dateBeginVacation03")._d
              );

              setFieldsValue({
                dateEndVacation02: datEndP2,
                datePgto02: datePgo2,
                daysVacation03: diffDays,
                dateEndVacation03: datEndP3,
                datePgto03: datePgo3,
              });
            }

            return Promise.resolve();
          }
        } else {
          return Promise.reject(
            new Error(
              "Favor primeiro selecionar a data de inicio das férias corretamente."
            )
          );
        }
      }
    },
  });

  return { dayVacationP2 };
}
