export const convertStringDate = (dateString: string) => {
  const strDate = dateString;
  let dateStr: string = "";
  if (strDate !== undefined) {
    dateStr =
      strDate.substring(6, 10) +
      "," +
      strDate.substring(3, 5) +
      "," +
      strDate.substring(0, 2);
  }
  const dateRef = new Date(dateStr);
  //console.log(dateStr);
  //console.log(dateRef);

  return dateRef;
};
