export const DayVacationUnexcused = ({ setFieldsValue }) => ({
  validator(_, value) {
    if (value <= 5) {
      setFieldsValue({
        vacationDays: 30,
        daysVacation01: 30,
      });
    } else if (value > 5 && value <= 14) {
      setFieldsValue({
        vacationDays: 24,
        daysVacation01: 24,
      });
    } else if (value > 14 && value <= 23) {
      setFieldsValue({
        vacationDays: 18,
        daysVacation01: 18,
      });
    } else if (value > 23 && value <= 32) {
      setFieldsValue({
        vacationDays: 12,
        daysVacation01: 12,
      });
    } else {
      setFieldsValue({
        vacationDays: 0,
        daysVacation01: 0,
      });
    }
  },
});
