import {
  Input,
  InputNumber,
  Form,
  Row,
  Col,
  Tooltip,
  DatePicker,
  Radio,
  Space,
  Checkbox,
} from "antd";
import { useEffect, useState } from "react";
import "../containersCommonFile/container.css";
import { FieldData } from "../../../util/containerInterface";
import { textData, textDays } from "../../../util/warningNotes";
import { DayVacationUnexcused } from "../../../util/validation/dayVacationUnexcused";
import { ValidationDateP1 } from "../../../util/validation/validationDateP1";
import { ValidationDateP2 } from "../../../util/validation/validationDateP2";
import { ValidationDateP3 } from "../../../util/validation/validationDateP3";
import { ValidationDayVacationP1 } from "../../../util/validation/validationDayVacationP1";
import { ValidationDayVacationP2 } from "../../../util/validation/validationDayVacationP2";

export default function ContainerVacation(props: any) {
  const [fieldsForm, setFieldsForm] = useState<FieldData[]>([
    { name: "periodEnd", value: "" },
    { name: "unexcused", value: "" },
    { name: "vacationDays", value: "" },
    { name: "dateBeginVacation01", value: "" },
    { name: "daysVacation01", value: "" },
    { name: "dateEndVacation01", value: "" },
    { name: "datePgto01", value: "" },
    { name: "dateBeginVacation02", value: "" },
    { name: "daysVacation02", value: "" },
    { name: "dateEndVacation02", value: "" },
    { name: "datePgto02", value: "" },
    { name: "dateBeginVacation03", value: "" },
    { name: "daysVacation03", value: "" },
    { name: "dateEndVacation03", value: "" },
    { name: "datePgto03", value: "" },
    { name: "abonoPec", value: "" },
    { name: "dtsal", value: "" },
  ]);

  const { dayVacationP1 } = ValidationDayVacationP1();
  const { dayVacationP2 } = ValidationDayVacationP2();

  interface enaRowProps {
    enaRow01: boolean;
    enaRow02: boolean;
  }

  const [enaRow, setEnaRow] = useState<enaRowProps>({
    enaRow01: true,
    enaRow02: true,
  });

  const { dateValidationP1 } = ValidationDateP1();
  const { dateValidationP2 } = ValidationDateP2();
  const { dateValidationP3 } = ValidationDateP3();

  const [abono, setAbono] = useState(false);

  const [form] = Form.useForm();

  useEffect(() => {
    props.set({
      show01: true,
      show02: true,
    });
  }, [enaRow]);

  //console.log(props.ena);

  //console.log(enaRow);

  return (
    <>
      <fieldset>
        <legend>Seleção de Férias</legend>

        <Form
          name="formVacation"
          labelAlign="left"
          fields={fieldsForm}
          form={form}
          onFieldsChange={(_, allfields) => {
            setFieldsForm(allfields);
            //console.log(fieldsForm);
          }}
        >
          <Col span={20}>
            <Row justify="space-around">
              <Form.Item label={"Fim do Período Aquisitivo"} name={"periodEnd"}>
                <Input style={{ width: "120px" }} readOnly />
              </Form.Item>

              <Form.Item
                name={"unexcused"}
                label={"Faltas não justificadas"}
                rules={[DayVacationUnexcused(form)]}
              >
                <Input style={{ width: "60px" }} />
              </Form.Item>

              <Form.Item
                name={"vacationDays"}
                label={"Dias Aquisitivos de Férias"}
                labelAlign="right"
              >
                <Input style={{ width: "60px" }} readOnly />
              </Form.Item>
            </Row>
            <Row>
              <Form.Item
                name={"abonoPec"}
                rules={[
                  ({ getFieldValue, setFieldsValue }) => ({
                    validator(_, value) {
                      //console.log(value);
                      //console.log(abono);

                      if (abono) {
                        const third = (getFieldValue("vacationDays") / 3) * 2;
                        setFieldsValue({
                          vacationDays: third,
                          daysVacation01: third,
                        });
                      } else {
                        const dayvac = (getFieldValue("vacationDays") / 2) * 3;
                        setFieldsValue({
                          vacationDays: dayvac,
                          daysVacation01: dayvac,
                        });
                      }
                    },
                  }),
                ]}
              >
                <Checkbox
                  onChange={(value) => {
                    setAbono(value.target.checked);
                    //console.log(value);
                  }}
                  value={"checked"}
                >
                  Abono
                </Checkbox>
              </Form.Item>
            </Row>
          </Col>
          <Row>
            <Col span={20}>
              <Row justify="space-around">
                <Tooltip placement="top" title={textData}>
                  <Form.Item
                    label={"Data Início"}
                    name={"dateBeginVacation01"}
                    rules={[
                      {
                        required: true,
                        message:
                          "Obrigatório informar a data de inicio das férias.",
                      },
                      dateValidationP1(form),
                    ]}
                  >
                    <DatePicker
                      style={{ width: "120px" }}
                      allowClear
                      format="DD/MM/YYYY"
                    />
                  </Form.Item>
                </Tooltip>

                <Tooltip placement="top" title={textDays}>
                  <Form.Item
                    label={"Qto Dias"}
                    name={"daysVacation01"}
                    rules={[
                      {
                        required: true,
                        message: "Favor informar o número de dias de férias.",
                      },
                      dayVacationP1(form, setEnaRow),
                    ]}
                  >
                    <InputNumber
                      min={5}
                      max={
                        form.getFieldValue("daysVacation02") < 6 &&
                        form.getFieldValue("daysVacation03") === 5
                          ? form.getFieldValue("vacationDays") -
                            form.getFieldValue("daysVacation02") -
                            form.getFieldValue("daysVacation03")
                          : form.getFieldValue("vacationDays")
                      }
                      style={{ width: "60px" }}
                    />
                  </Form.Item>
                </Tooltip>
                <Form.Item label={"Data Fim"} name={"dateEndVacation01"}>
                  <Input style={{ width: "120px" }} readOnly />
                </Form.Item>
                <Form.Item label={"Data Pgto"} name={"datePgto01"}>
                  <Input style={{ width: "120px" }} readOnly />
                </Form.Item>
              </Row>

              <div
                hidden={!enaRow.enaRow01 || !props.ena.show01 ? false : true}
              >
                <Row justify="space-around">
                  <Tooltip placement="top" title={textData}>
                    <Form.Item
                      label={"Data Início"}
                      name={"dateBeginVacation02"}
                      rules={[
                        {
                          required: true,
                          message:
                            "Obrigatório informar a data de inicio das férias.",
                        },
                        dateValidationP2(form),
                      ]}
                    >
                      <DatePicker
                        style={{ width: "120px" }}
                        allowClear
                        format="DD/MM/YYYY"
                      />
                    </Form.Item>
                  </Tooltip>
                  <Tooltip placement="top" title={textDays}>
                    <Form.Item
                      label={"Qto Dias"}
                      name={"daysVacation02"}
                      rules={[
                        {
                          required: true,
                          message: "Favor informar o número de dias de férias.",
                        },
                        dayVacationP2(form, setEnaRow),
                      ]}
                    >
                      <InputNumber
                        min={5}
                        max={
                          form.getFieldValue("vacationDays") -
                          form.getFieldValue("daysVacation01")
                        }
                        style={{ width: "60px" }}
                      />
                    </Form.Item>
                  </Tooltip>

                  <Form.Item label={"Data Fim"} name={"dateEndVacation02"}>
                    <Input style={{ width: "120px" }} readOnly />
                  </Form.Item>
                  <Form.Item label={"Data Pgto"} name={"datePgto02"}>
                    <Input style={{ width: "120px" }} readOnly />
                  </Form.Item>
                </Row>
              </div>
              <div
                hidden={!enaRow.enaRow02 || !props.ena.show02 ? false : true}
              >
                <Row justify="space-around">
                  <Tooltip placement="top" title={textData}>
                    <Form.Item
                      label={"Data Início"}
                      name={"dateBeginVacation03"}
                      rules={[
                        {
                          required: true,
                          message:
                            "Obrigatório informar a data de inicio das férias.",
                        },
                        dateValidationP3(form),
                      ]}
                    >
                      <DatePicker
                        style={{ width: "120px" }}
                        allowClear
                        format="DD/MM/YYYY"
                      />
                    </Form.Item>
                  </Tooltip>
                  <Form.Item label={"Qto Dias"} name={"daysVacation03"}>
                    <InputNumber style={{ width: "60px" }} readOnly />
                  </Form.Item>
                  <Form.Item label={"Data Fim"} name={"dateEndVacation03"}>
                    <Input style={{ width: "120px" }} readOnly />
                  </Form.Item>
                  <Form.Item label={"Data Pgto"} name={"datePgto03"}>
                    <Input style={{ width: "120px" }} readOnly />
                  </Form.Item>
                </Row>
              </div>
            </Col>
            <Col span={4}>
              <Row>
                <Form.Item name={"dtsal"} style={{ marginTop: "-55px" }}>
                  <Radio.Group>
                    <Space
                      direction="vertical"
                      size={35}
                      style={{ display: "flex", marginTop: "-55px" }}
                    >
                      <Radio value={0} checked>
                        Não
                      </Radio>
                      <Radio value={1}>13 Sal</Radio>
                      <span
                        hidden={
                          !enaRow.enaRow01 || !props.ena.show01 ? false : true
                        }
                      >
                        <Radio value={2}>13 Sal</Radio>
                      </span>
                      <span
                        hidden={
                          !enaRow.enaRow02 || !props.ena.show01 ? false : true
                        }
                      >
                        <Radio value={3}>13 Sal</Radio>
                      </span>
                    </Space>
                  </Radio.Group>
                </Form.Item>
              </Row>
            </Col>
          </Row>
        </Form>
      </fieldset>
    </>
  );
}
