import { WarningNotes } from "../warningNotes";
import { convertStringDate } from "../historyInfoVacationFunction/historyInfoVacationFunction";

interface ValidatorDaysProps {
  value: number;
  name: string;
  dayVacation: number;
  dayP1: number;
  dayP2: number;
  setEnaRow: () => void;
}
export function ValidationDatesVacation() {
  //Função para validação de dias
  const validatorDays = (
    value,
    name,
    dayVacation,
    dayP1,
    dayP2,
    dayP3,
    setEnaRow
  ) => {
    let diffDays = 0;
    //console.log(value);
    //console.log(name);
    //console.log(dayVacation);
    //console.log(dayP1);
    //console.log(dayP2);
    //console.log(typeof dayP3);
    if (name === "daysVacation01") {
      diffDays = dayVacation - value;

      if (
        (value >= 14 || diffDays >= 14) &&
        value <= dayVacation - 5 &&
        value >= 5
      ) {
        setEnaRow({
          enaRow01: false,
          enaRow02: true,
        });
        return diffDays;
      } else if (dayP2 < 5 || dayP2 === undefined || diffDays < 5) {
        setEnaRow({
          enaRow01: true,
          enaRow02: true,
        });
      }
    } else if (name === "daysVacation02") {
      diffDays = dayVacation - dayP1 - value;
      console.log("daysVacation02");
      console.log(diffDays);

      if (diffDays < 5) {
        //console.log("Desabilita div 3");
        setEnaRow({
          enaRow01: false,
          enaRow02: true,
        });
      } else if (
        value >= 14 ||
        diffDays === 14 ||
        (value >= 5 && value <= dayVacation - dayP1 - 5)
      ) {
        //console.log("habilita as duas divs 2 e 3");
        setEnaRow({
          enaRow01: false,
          enaRow02: false,
        });
      }
    } else if (name === "daysVacation03") {
      //console.log("habilita as duas divs 2 e 3");
      diffDays = dayVacation - parseInt(dayP3) - value;
      //console.log(diffDays);
      setEnaRow({
        enaRow01: false,
        enaRow02: false,
      });
    } else {
      //console.log("desabilita as duas divs 2 e 3");
      setEnaRow({
        enaRow01: true,
        enaRow02: true,
      });
    }
    return diffDays;
  };

  //Função para validação de data
  function validatorDate(dateBegin, dateEndBefore) {
    if (dateBegin === "") {
      return false;
    } else {
      const dayweek = dateBegin._d.getDay();

      if (dayweek === 0) {
        const aviso =
          "Dia de inicio da férias cai no domingo. Data não permitida. Favor corrigir";
        WarningNotes(aviso);
      } else if (dayweek === 6) {
        const aviso =
          "Dia de inicio da férias cai no sábado. Data não permitida. Favor corrigir";
        WarningNotes(aviso);
      }
      if (dateBegin) {
        const chosenDate = new Date(dateBegin._d);
        const today = new Date();
        let diff = Math.abs(chosenDate.getTime() - today.getTime());
        let dias = Math.ceil(diff / (1000 * 60 * 60 * 24));

        if (dias < 60) {
          const aviso =
            "Não é permitido escolher data abaixo de 60 dias após a data de requisição. Por favor verifique o preenchimento";

          WarningNotes(aviso);
          return false;
        } else if (dateEndBefore) {
          const refDate = new Date(convertStringDate(dateEndBefore));

          const newChosenDate = new Date(
            chosenDate.getFullYear(),
            chosenDate.getMonth(),
            chosenDate.getDate()
          );

          if (newChosenDate <= refDate) {
            if (
              newChosenDate.getMonth() === refDate.getMonth() &&
              newChosenDate.getDate() <= refDate.getDate()
            ) {
              const aviso =
                "Data não pode ser inferior ou igual a data da parcela anterior. ";
              WarningNotes(aviso);
            } else if (newChosenDate.getMonth() < refDate.getMonth()) {
              const aviso =
                "Mês não pode ser inferior ao mês da parcela anterior. Favor corrigir.";
              WarningNotes(aviso);
            }
            return false;
          } else {
            return true;
          }
        } else {
          return true;
        }
      }
    }
  }

  return {
    validatorDays,
    validatorDate,
  };
}
